.solar_systm {
    transform-style: preserve-3d;
    pointer-events: none;
    height: 100%;
    position: absolute;
    left: 0;
    right: 0;
}

* {
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

.solar {
    position: relative;
    width: 100%;
    height: 100%;
}

.menu {
    color: white;
    display: block;
    position: absolute;
    cursor: pointer;
    left: 90px;
    z-index: 2;
}

.preview {
    width: 30px;
    height: 30px;
    background: yellow;
    float: left;
    background-size: auto 100% !important;
    position: absolute;
    border-radius: 100px;
}

.info {
    position: relative;
    left: 50px;
    top: 1px;
}

h2, h3 {
    text-transform: uppercase;
    margin: 0;
    font-weight: 100;
    letter-spacing: 2px;
}

h2 {
    font-size: 11px;
    opacity: 0.4;
    margin-bottom: 4px;
}

h3 {
    font-size: 8px;
    letter-spacing: 1px;
    opacity: 0.3;
}

.pip {
    width: 0;
    height: 9px;
    background: #fea082;
    float: left;
    position: relative;
    top: 3px;
    transition: all 0.3s;
    margin-right: 0px;
}

.planet_description {
    text-align: center;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    color: white;
}

.planet_description h1 {
    font-size: 24px;
    margin-bottom: 10px;
}

.planet_description h2 {
    font-size: 16px;
    margin-bottom: 5px;
}

.planet_description p {
    font-size: 12px;
    line-height: 1.5;
}

.planet9, .planet8, .planet7, .planet6, .planet5, .planet4, .planet3, .planet2, .planet1 {
    appearance: none;
    position: absolute;
    width: 16px;
    height: 16px;
    border: 2px solid white;
    outline: none;
    border-radius: 20px;
}